.tab {
     display: flex;
     padding: 0.6rem 0.8rem;
     border: solid 1px var(--navigation-secondary-color);
     border-left: none;
     border-bottom: none;
     align-items: center;
     background-color: var(--navigation-bg-color);
     color: var(--navigation-fg-secondary-color);     
}

.tab:hover {
     cursor: pointer;
}

.tab .tab-title {
     max-width: 150px;
     text-overflow: ellipsis;
     white-space: nowrap;
     overflow: hidden;
}

.tab .close-icon {
     margin-left: 0.5rem;
     padding: 0.1rem;
     border-radius: 3px;
}

.tab .close-icon:hover {
     background-color: var(--navigation-secondary-color);
}

.tab-icon {
     padding-right: 0.3rem;
}

.curly-braces {
     fill: yellow;
}

.ts-icon {
     fill: skyblue;
}

.html-icon {
     fill: orangered;
}

.rust-icon {
     fill: gray;
}