.navbar {
     font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
     min-width: 20vw;
     max-width: 20vw;
     overflow-x: scroll;
     white-space: nowrap;
     height: 100vh;
     border-right: solid 1px;
     border-color: var(--navigation-secondary-color);
     padding: 1rem;
     color: var(--navigation-fg-primary-color);
     background-color: var(--navigation-bg-color);
}

.readable-navbar-item {
     display: flex;
     flex-direction: row;
     padding-left: 1.3rem !important;
     align-items: center;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
}

.readable-navbar-single {
     display: flex;
     flex-direction: row;
     padding: 0.3rem 0 0.3rem 0.3rem;
     align-items: center;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
     width: 100%;
}

.expandable-main, .readable-navbar-item {
     padding: 0.3rem 0;
}

.expandable-main {
     display: flex;
     align-items: center;
}

.expandable-main:hover, .readable-navbar-item:hover, .single-readable:hover {
     background-color: #3a3838;
     cursor: pointer;
}

.expand-btn {
     border: none;
}

.single-readable {
     padding-left: 0.1rem;
     display: flex;
     flex-direction: row;
     align-items: center;
}

.curly-braces {
     fill: yellow;
}

.readable-icon {
     padding-right: 0.3rem;
}

.ts-icon {
     fill: skyblue;
}

.html-icon {
     fill: orangered;
}

.rust-icon {
     fill: gray;
}