:root {
     --navigation-bg-color: #000000;
     --navigation-fg-primary-color: #ffffff;
     --canvas-bg-color: #1e1e1e;
     --navigation-secondary-color: #575b64;
     --navigation-fg-secondary-color: #b5bac0;
     --active-tab-color: #d56d2d;
}

.app-main {
     display: flex;
     overflow: hidden;
     background-color: var(--canvas-bg-color);
     font-size: 14px;
}

@font-face {
     font-family: 'CustomMonospace';
     src: local('Consolas'), local('Courier New'), monospace;
}