.central-canvas {
     display: flex;
     flex-direction: column;
     background-color: var(--canvas-bg-color);
     width: 80vw;
}

.toolbar {
     display: flex;
     height: 38px;
     background-color: var(--navigation-bg-color);
     border-bottom: solid 1px var(--navigation-secondary-color)
}

.tab-toolbar {
     font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
     display: flex;
     overflow-x: auto;
     white-space: nowrap;
     height: 38px;
     max-width: 75vw;
     background-color: var(--navigation-bg-color);
     border-bottom: solid 1px var(--navigation-secondary-color);
}

.run-icon-wrapper {
     display: flex;
     align-items: center;
     margin-left: auto;
     margin-right: 1rem;
     color: var(--navigation-fg-secondary-color);
     justify-content: flex-end;
}

.run-icon, .manual-run-icon {
     cursor: pointer;
     fill: green;
     padding: 0.2rem;
     border-radius: 3px;
}

.run-icon:hover {
     cursor: pointer;
     fill: var(--navigation-fg-secondary-color);
     background-color: green;
}

.rendered-content-wrapper {     
     border-left: solid 1.5px var(--navigation-fg-secondary-color);
     color: var(--navigation-fg-primary-color);
}

.rendered-content {
     font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
     font-size: 20px;
     margin-left: 1rem;
     padding: 1rem;
}

.content {
     max-width: 77vw;
     display: flex;
     height: 100vh;
}

.portfolio-manual-content {
     display: flex;
     flex-direction: column;
     row-gap: 1rem;
     color: var(--navigation-fg-secondary-color);
     height: 90vh;
     width: 77vw;
     align-items: center;
     justify-content: center;
}

.manual-icon {
     background-color: var(--navigation-bg-color);
     padding: 0.4rem;
     border-radius: 5px;
     color: var(--navigation-fg-primary-color);
}